

















import Vue from "vue";
// @ is an alias to /src
export default Vue.extend({
  name: "ProjectTile",
  props: {
    url: String,
    title: String
  },
  mounted() {
    this.tileStyle = `background-image: url('~@/assets/images/${this.url}');`;
  },
  components: {},
  data: () => ({
    tileStyle: "",
    hovering: false
  })
});
